.welcomeSplash {
  z-index: 10;
  height: auto;
  width: 30%;
  max-width: 450px;
  border-radius: 20px;
  border: 2px solid $primaryColour;
  box-shadow: 2px 2px 4px $bgColour;
  margin: 75px auto 0 auto;
  background: $white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  h1 {
    text-align: center;
    color: $accentColour;
  }
  p {
    color: lighten($black, 30%);
    text-align: center;
    letter-spacing: 0.2rem;
  }
  .miniText {
    color: $primaryColour;
    font-size: 1.2rem;
    font-weight: bold;
    margin: 15px 0 0 0;
    letter-spacing: normal;
    text-transform: uppercase;
  }
  .disclaimerText {
    color: lighten($black, 40%);
    font-size: 1.2rem;
    letter-spacing: normal;
    font-style: italic;
  }
}
.userLoginHome {
  text-align: center;
  color: $accentColour;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 40px;
}

.logo {
  display: block;
  width: 40%;
  max-width: 120px;
  margin: 40px auto 20px auto;
}

.bounce-in-fwd {
  -webkit-animation: bounce-in-fwd 1.1s both;
  animation: bounce-in-fwd 1.1s both;
}

